.red {
  background-color: red !important;
}

.yellow {
  background-color: yellow !important;
}

.slot {
  box-sizing: border-box;
  width: 80px;
  height: 80px;
  margin: 10px;
  border-radius: 100%;
  background-color: white;
}

.highlight {
  background-color: lightgray;
  border: 6px dashed orange;
}
