body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

body {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.container {
  position: relative;
  width: 700px;
  height: 700px;
}

.arrow-container {
  float: left;
  width: 100%;
  padding-top: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.column {
  width: 100px;
  height: 600px;
  float: left;
  padding: 0;
  margin: 0;
  background-color: blue;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.red {
  background-color: red !important;
}

.yellow {
  background-color: yellow !important;
}

.slot {
  box-sizing: border-box;
  width: 80px;
  height: 80px;
  margin: 10px;
  border-radius: 100%;
  background-color: white;
}

.highlight {
  background-color: lightgray;
  border: 6px dashed orange;
}

.dragrow {
  width: 700px;
  height: 100px;
  float: left;
  padding: 0;
  margin: 0;
}

.draggableDiv {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100px;
  height: 100px;
}

.piece {
  width: 80px;
  height: 80px;
  margin: 10px;
  border-radius: 100%;
}

.red {
  background-color: red;
}

.yellow {
  background-color: yellow;
}

.prompt > .cursor {
  z-index: 9999;
  width: 32px;
  height: 32px;
  position: absolute;
  left: 45px;
  top: 45px;
  background-position: center;
  background-size: cover;
  -webkit-animation: moveCursor 1.5s ease-in infinite;
          animation: moveCursor 1.5s ease-in infinite;
}

.prompt > .prompt-message {
  z-index: 9999;
  position: absolute;
  left: 170px;
  top: 5px;
  margin: 0;
  padding: 0;
  color: green;
  font-family: Helvetica;
  font-size: 30px;
  font-weight: bold;
  text-shadow: -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black;
  -webkit-animation: moveMessage 1s linear alternate infinite;
          animation: moveMessage 1s linear alternate infinite;
}

@-webkit-keyframes moveCursor {
  0% {
    left: 45px;
    background-image: url(/react-connect4/static/media/click.1451830d.png);
  }
  50% {
    background-image: url(data:image/png;base64,);
  }
  100% {
    left: 150px;
  }
}

@keyframes moveCursor {
  0% {
    left: 45px;
    background-image: url(/react-connect4/static/media/click.1451830d.png);
  }
  50% {
    background-image: url(data:image/png;base64,);
  }
  100% {
    left: 150px;
  }
}

@-webkit-keyframes moveMessage {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}

@keyframes moveMessage {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}

.message {
  position: absolute;
  font-family: Helvetica;
  font-size: 100px;
  width: 700px;
  height: 700px;
  left: 50%;
  margin-left: -350px;
  top: 50%;
  margin-top: -300px;
  text-align: center;
  line-height: 700px;
  text-shadow: -5px 0 black, 0 5px black, 5px 0 black, 0 -5px black;
}

.red-font {
  color: red;
}

.yellow-font {
  color: yellow;
}

.arrow-left {
  width: 0;
  height: 0;
  border-top: 40px solid transparent;
  border-bottom: 40px solid transparent;
  border-right: 40px solid orange;
  border-right-width: 80px;
  cursor: pointer;
}

.arrow-right {
  width: 0;
  height: 0;
  border-top: 40px solid transparent;
  border-bottom: 40px solid transparent;
  border-left: 40px solid orange;
  border-left-width: 80px;
  cursor: pointer;
}

